/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  background: #002222;
  margin: 0;
}

section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100vw;
  min-height: 110vh;
  overflow: hidden;
}
@media (min-width: 1007px) {
  section {
    justify-content: flex-start;
    height: 100vh;
  }
}
section .container {
  min-height: 100%;
}
@media (min-width: 1007px) {
  section .container {
    min-height: 90%;
  }
}

.container {
  position: relative;
  width: 95%;
  max-width: 240rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 1007px) {
  .container {
    flex-direction: row;
    align-items: center;
  }
}

a {
  font-family: "Bebas Neue", cursive;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
}

p {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
  margin: 0;
}

h1,
h2,
h3,
h4 {
  font-family: "Lato", sans-serif;
  margin: 10em;
  color: black;
  margin: 0;
  font-weight: 500;
  text-transform: uppercase;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2.4rem;
}

h4 {
  font-size: 1.8rem;
  margin: 0;
}

#about,
#work {
  background-color: #FFF0D3;
}

.personal-links {
  position: absolute;
  display: flex;
  z-index: 1;
}
.personal-links img {
  width: 9vw;
  max-width: 4rem;
}
@media (min-width: 1007px) {
  .personal-links img {
    width: 3vw;
    min-width: 5rem;
  }
}

.bg-img {
  max-width: 100%;
  height: 100vh;
}

.item-unvisible {
  visibility: hidden;
}
@media (min-width: 1007px) {
  .item-unvisible {
    visibility: visible;
  }
}

.item-visible {
  visibility: visible;
}

.text {
  text-align: justify;
  line-height: 1.8;
  padding: 2rem;
}
.text h4 {
  color: #000000;
  font-size: 0.95em;
}
@media (min-width: 1007px) {
  .text {
    width: 90%;
    height: auto;
    margin: 0.5rem 1.5rem;
    margin-right: 3rem;
    padding: 0 5rem;
    text-align: justify;
  }
  .text h4 {
    font-size: 1.3em;
  }
}

header {
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 101;
  overflow: hidden;
}

header .container {
  width: 100%;
}

.navbar-logo {
  animation: navbar-logo 2s cubic-bezier(0.5, 0.6, 0.6, 1);
  animation-fill-mode: backwards;
}
.navbar-logo h1 {
  font-family: "Bebas Neue", cursive;
  font-size: 6rem;
  color: #7f1a35;
  transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
}
.navbar-logo h1 span {
  color: #FFF0D3;
}
.navbar-logo h2 {
  padding-left: 1.3rem;
  margin-top: -4.7rem;
  text-align: center;
  font-family: "Bebas Neue", cursive;
  font-size: 3rem;
  color: #7f1a35;
  transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
}
.navbar-logo h2 span {
  color: #FFF0D3;
}
@media (min-width: 1007px) {
  .navbar-logo h1 {
    font-size: 6em;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
  }
  .navbar-logo h1 span {
    color: #FFF0D3;
  }
  .navbar-logo h2 {
    padding-left: 2.5rem;
    margin-top: -8rem;
    text-align: center;
    font-family: "Bebas Neue", cursive;
    font-size: 3em;
    color: #7f1a35;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
  }
  .navbar-logo h2 span {
    color: #FFF0D3;
  }
}

@keyframes navbar-logo {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.checkbox {
  position: absolute;
  display: block;
  height: 32px;
  width: 32px;
  top: 12vh;
  left: 45vw;
  z-index: 5;
  opacity: 0;
  cursor: pointer;
}
@media (min-width: 1007px) {
  .checkbox {
    display: none;
  }
}

.hamburger-lines {
  display: block;
  height: 26px;
  width: 32px;
  margin: 1rem 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 1007px) {
  .hamburger-lines {
    display: none;
  }
}

.line {
  display: block;
  height: 4px;
  width: 100%;
  border-radius: 1rem;
  background: #dfe1e2;
}
@media (min-width: 1007px) {
  .line {
    display: none;
  }
}

.line1 {
  transform-origin: 0% 0%;
  transition: transform 0.4s ease-in-out;
}

.line2 {
  transition: transform 0.2s ease-in-out;
}

.line3 {
  transform-origin: 0% 100%;
  transition: transform 0.4s ease-in-out;
}

.navbar-menu {
  display: none;
  padding-top: 9rem;
  background-color: #002222;
  height: 100vh;
  width: 100%;
  margin-top: 1rem;
  flex-direction: column;
  text-align: center;
}
.navbar-menu a {
  color: #FFF0D3;
  padding: 0rem 2rem;
  font-size: 3rem;
}
@media (min-width: 1007px) {
  .navbar-menu {
    padding: 0;
    box-shadow: none;
    display: flex;
    position: fixed;
    height: initial;
    margin: 0;
    transition: none;
    transform: none;
    top: 85vh;
    flex-direction: row;
    justify-content: center;
    background-color: unset;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 0.35s;
  }
  .navbar-menu a {
    padding: 0.45rem 2.5rem 0 2.5rem;
    font-size: 5vw;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 0.8s;
  }
  .navbar-menu .active {
    background-color: #005555;
  }
  .navbar-menu a:hover {
    background-color: rgba(0, 0, 0, 0.6);
  }
}

@keyframes navbar-menu {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.visibleMenu {
  display: flex;
}

.lineAnimation .line1 {
  transform: rotate(45deg);
}
.lineAnimation .line2 {
  transform: scaleY(0);
}
.lineAnimation .line3 {
  transform: rotate(-45deg);
}

.move-header {
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
}
@media (min-width: 1007px) {
  .move-header {
    padding: 0 !important;
  }
}
.move-header .navbar-logo h1 {
  font-size: 3.5rem;
  transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
}
.move-header .navbar-logo h2 {
  padding-left: 1.1rem;
  margin-top: -2.8rem;
  font-size: 1.7rem;
  transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
}
@media (min-width: 1007px) {
  .move-header .navbar-logo h1 {
    font-size: 3em;
    transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
  }
  .move-header .navbar-logo h2 {
    padding-left: 1.13rem;
    margin-top: -4rem;
    font-size: 1.5em;
    transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
  }
}
@media (min-width: 1007px) {
  .move-header .navbar-menu {
    top: 90%;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 1.5s;
  }
  .move-header .navbar-menu a {
    font-size: 2.2vw;
  }
}

@media (min-width: 1007px) {
  .change-color a {
    color: #7f1a35;
  }
  .change-color a:hover {
    background-color: rgba(45, 0, 0, 0.8);
    color: #FFF0D3;
  }
  .change-color .active {
    color: #FFF0D3;
    background-color: #7f1a35;
  }
}

#home {
  min-height: 100vh;
}

.personal-img {
  z-index: 100;
  position: fixed;
  top: 55%;
  left: 7%;
  right: 0;
  bottom: 0;
  background-image: url(../../../src/img/test2.png);
  background-size: cover;
  background-repeat: no-repeat;
  animation: personal-img 1.2s cubic-bezier(0.5, 0.6, 0.6, 1);
  animation-fill-mode: backwards;
}
@media (min-width: 1007px) {
  .personal-img {
    top: 17%;
    left: 10%;
  }
}

@keyframes personal-img {
  0% {
    opacity: 0;
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.move-img {
  position: absolute;
}

.about-graph {
  display: flex;
  width: 100%;
  height: 35vh;
  justify-content: center;
  margin-bottom: -5rem;
}
.about-graph img {
  width: auto;
  height: 100%;
}
@media (min-width: 1007px) {
  .about-graph {
    justify-content: center;
    width: 80vw;
    height: 50vh;
    margin: 0 1rem;
    overflow: hidden;
  }
}

.social {
  top: 95%;
  column-gap: 3rem;
}
@media (min-width: 1007px) {
  .social {
    top: 70%;
    column-gap: 5rem;
  }
}

.swiper {
  width: 85%;
  height: 30vh;
}
@media (min-width: 1007px) {
  .swiper {
    width: 40%;
    height: 55%;
  }
}

.swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(../../../src/img/LaIsleta.JPG);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  /* border-radius: 18px; */
  font-size: 22px;
  font-weight: bold;
  color: #FFF0D3;
}

.github {
  margin-bottom: 1rem;
}
.github img {
  width: auto;
  height: 6vh;
  min-height: 4rem;
}
@media (min-width: 1007px) {
  .github {
    position: absolute;
    top: 70%;
  }
}

#skills .container h3 {
  color: #FFF0D3;
  text-align: center;
}
#skills .container h4 {
  color: #FFF0D3;
  margin-bottom: 2rem;
  text-align: center;
}
#skills .container p {
  color: #FFF0D3;
}
@media (min-width: 1007px) {
  #skills .container {
    flex-direction: column;
    row-gap: 5rem;
    margin-top: -4rem;
  }
  #skills .container h4 {
    margin-bottom: 3rem;
  }
}

.tools {
  width: 75%;
  height: 10rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 2.5rem;
}
@media (min-width: 1007px) {
  .tools {
    width: 100%;
  }
}

.col {
  display: flex;
  justify-content: center;
  column-gap: 5rem;
  height: auto;
}
.col img {
  width: auto;
  height: 4vh;
  max-height: 4rem;
}
@media (min-width: 1007px) {
  .col img {
    height: 6vh;
    max-height: 10rem;
  }
}